            <my-header/>
            <my-search/>
        <el-main>
            <router-view/>
        </el-main>
        <el-footer class="myFooter">
            <my-footer/>
        </el-footer>
    </el-container>
</template>
<script>
import MyHeader from '@/components/template/Header.vue'
import MySearch from '@/components/template/search/Search.vue'
