import Vue from 'vue'
import Router from 'vue-router'
import MainBody from '@/components/template/MainBody.vue'
import HomePage from '@/components/RouterView/HomePage.vue'
const ChangeCity = () => import('@/components/RouterView/changeCity/ChangeCity.vue')
const GoodsList = () => import('@/components/RouterView/goodsList/GoodsList.vue')
const Landing = () => import('@/components/template/Landing.vue')
const Register = () => import('@/components/template/Register.vue')
Vue.use(Router)

export default new Router({
  mode:'history',
  routes: [
     {
       path:'/',
       redirect:'/mainbody'
     },
     {
       path:'/mainbody',
       name:'mainbody',
       component:MainBody,
       redirect:'/mainbody/homepage',
       children:[
         {
           path:'homepage',
           name:'homepage',
           component:HomePage
         },
         {
           path:'changeCity',
           name:"changeCity",
           component:ChangeCity
         },
         {
           path:"goodsList/:name",
           name:"goodsList",
           component:GoodsList
         }
       ]
     },
     {
       path:'/landing',
       name:'landing',
       component:Landing
     },
     {
       path:"/register",
       name:'register',
       component:Register
     }
  ]
})



