<template>
    <el-header height="" class="search-warpper">
        <el-row class="search-row">
           <el-col :span="6" class="logo-col">
               <img src="//s0.meituan.net/bs/fe-web-meituan/e5eeaef/img/logo.png" alt="美团">
           </el-col>
           <el-col :span="18" class="search-input">
               <div class="search-div">
                <input type="text" name="search" id="header-search" placeholder="搜索商家或地点" 
                @blur="myBlur"
                autocomplete="off"
                v-model="searchValue"
                @focus="myFocus"
                @input="searchFilter"/>
                <router-link type="primary" class="search-btn el-button"
                           tag="button" :to="{name:'goodsList',params:{name:'+searchValue+'}}">
                <i class="el-icon-search"></i>
                </router-link>
                <dl class="recommend-none" v-if="isHasContent">
                    <dt>热门搜索</dt>
                    <dd v-for="(item,index) in recommendList" :key="index">
                        <a href="#">{{item}}</a>
                    </dd>
                </dl>
                <!-- 推荐搜索框 -->
                <dl class="search-none" v-if="isHasSearch">
                  
                        <dd v-for="(item,index) in dataList" :key="index">
                        <a href="#">{{item}}</a>
                    </dd>
                   
                </dl>
               </div>
               <div class="recommend">
                   <a href="#" v-for="item in recommendList" :key="item">
                       {{item}}
                   </a>
               </div>
           </el-col>
          
       </el-row>
    </el-header>
</template>
<script>
import api from '@/axios.js'
export default {
    data(){
        return {
            recommendList:[],
            searchValue:'',
            isFocus:false,
            dataList:[],
           
        }
    },
    computed:{
       isHasContent(){
           return !this.searchValue && this.isFocus;
       },
       isHasSearch(){
           return this.searchValue && this.isFocus;
       }
    },
    methods:{
        myFocus(){
            this.isFocus = true;
        },
        myBlur(){
            setTimeout(()=>{
                this.isFocus = false;
            },200)
        },
        searchFilter(){
         
            api.getInputValue().then((res)=>{
                const ValueArr = res.data.data.list.filter((item,index)=>{
                    return item.indexOf(this.searchValue) > -1
                });
                this.dataList = ValueArr;
            })
        }
        
    },
    created(){
        api.getSearchHotWords().then((res)=>{
               const HotWordArr = res.data.data;
               this.recommendList = HotWordArr;
            })
    }
}
</script>
<style lang="less">
@import url('../../../assets/style/Header/search.less');
</style>





