<template>
    <ul class="geo">
        <li>
            <i class="el-icon-location"/><span>{{curCity}}</span>
        </li>
        <li class="changeCity">
            <router-link tag="a" :to="{name:'changeCity'}">
                切换城市
            </router-link>
        </li>
        <li class="my-geo">
            <span>[</span>
            <a href="#">大厂回族自治县</a>
            <a href="#">廊坊</a>
            <a href="#">固安县</a>
            <span>]</span>
        </li>
        <li class="login">
            <router-link tag="a" :to="{name:'landing'}">立即登陆</router-link>
        </li>
        <li class="registe">
            <router-link tag="a" :to="{name:'register'}">注册</router-link>
        </li>
    </ul>
</template>
<script>
import {mapState} from 'vuex'
export default {
    computed:{
        ...mapState(['curCity'])
    }
}
</script>
<style>

</style>





