const getters = {

}
const actions = {

}
const mutations = {
   changeCurCity(state,cityName){
       state.curCity = cityName;
   },
   isSelectedPush(state,{value,index}){
       if(index == -1){
          state.isSelectedArr.splice(0);
       }else{
         if(value == undefined){
            state.isSelectedArr.splice(index,1,'');
         }else{
            state.isSelectedArr[index] = value;
            state.isSelectedArr.splice(index,1,value);
         }
       }
   },
   changeCurPage(state,num){
       state.curPageIndex = num;
   },
   reorderGoodsList(state,active = '智能排序'){
       console.log(active);
       if(active == "价格最高"){
          
          state.goodsList.sort((a,b)=>{
              if(a.price == '暂无价格'){
                a.price = 0;
                  
              }else if(b.price == '暂无价格'){
                  b.price = 0;
              };
              return Number(b.price) - Number(a.price);
          })
       }else if(active == '价格最低'){
        state.goodsList.sort((a,b)=>{
            if(a.price == '暂无价格'){
              a.price = 0;
                
            }else if(b.price == '暂无价格'){
                b.price = 0;
            };
            return Number(a.price) - Number(b.price);
        })
       }else if(active == '智能排序'){
        //    state.goodsList = state.TwoGoodsList;
        console.log(state.TwoGoodsList)
       }
   }
   
}
export default new Vuex.Store({
    state,
    getters,
    actions,
    mutations
})


