             </el-col>
             <el-col :span="10" tag="div" offset="6">
                <Navigation/>
             </el-col>
       </el-row>
       </el-header>
  
</template>
<script>
import Geo from '@/components/template/nav/Geo.vue'
import Navigation from '@/components/template/nav/Navigation.vue'
export default {
    components:{
        Geo,
        Navigation
    }
}
</script>
<style lang="less">
@import url('../../assets/style/Header/Header.less');
</style>





